        <span v-if="state === 'social'">Follow</span>
        <span v-if="state === 'professional'" :class="{'hand-wrapper': true, friends: accepted}">
          <span id="hand-1">🤚</span>
          <span id="hand-text" :class="{hidden: accepted}">&nbsp;Connect</span>
          <span id="hand-2">✋</span></span>
        <span v-if="state === 'dating'"><font-awesome-icon icon="check"/></span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    state: String,
    screenClass: String,
    accepted: Boolean
  },
  methods: {
    denied() {
      this.$emit("denied");
    },
    primaryInteraction(e) {
      this.$emit("interaction", {state: this.state, e});
    }
  }
}
</script>

