<template>
  <UserCard
      :state="currentState"
      :screen-class="screenClass"
      :accepted="accepted"
      @denied="denied"
      @interaction="handleClickDependendOnState"
  ></UserCard>
  <div id="sites" :class="screenClass">
    <Social :class="screenClass" :screen-class="screenClass" v-model="accepted" ref="social"></Social>
    <Professional :class="screenClass" ref="professional"></Professional>
    <Dating :class="screenClass" :accepted="accepted" ref="dating"></Dating>
    <Footer></Footer>
  </div>

  <div v-show="accepted && currentState === 'social'" id="pic-wall" ref="pictureWall" :class="screenClass">
    <Picture :imgID="4" :active="accepted && currentState === 'social'"/>
    <Picture :imgID="1" :active="accepted && currentState === 'social'" class="big_sis"/>
    <Picture :imgID="2" :active="accepted && currentState === 'social'" class="big_boi"/>
    <Picture :imgID="3" :active="accepted && currentState === 'social'" v-if="screenClass === 'desktop'"/>
    <Picture :imgID="0" :active="accepted && currentState === 'social'" v-if="screenClass === 'desktop'" class="landscape"/>
    <Picture :imgID="5" :active="accepted && currentState === 'social'" v-if="screenClass === 'desktop'"/>
    <Picture :imgID="6" :active="accepted && currentState === 'social'" v-if="screenClass === 'desktop'"/>
    <Picture :imgID="7" :active="accepted && currentState === 'social'" v-if="screenClass === 'desktop'"/>
    <div id="abort" @click="deniedSocial()">
      &times;
    </div>
  </div>
</template>

<script>
import UserCard from './components/UserCard.vue'
import Social from "@/views/Social";
import Dating from "@/views/Dating";
import Professional from "@/views/Professional";
import Footer from "@/views/Footer";
import confetti from "canvas-confetti";
import Picture from "@/components/Picture";

export default {
  name: 'App',
  components: {
    Footer,
    Professional,
    Dating,
    Social,
    UserCard,
    Picture
  },
  data(){
    return {
      screenClass: null,
      accepted: false,
      possibleStates: ["social", "professional", "dating"],
      currentState: "social"
    }
  },
  created () {
    this.handleScroll();
    this.handleResize();
    window.addEventListener('scroll', this.handleScroll);
    window.addEventListener('resize', this.handleResize);
    window.addEventListener('orientationchange', this.handleResize);

    require('./assets/pic-wall/artsy.jpg')
    require('./assets/pic-wall/familie.jpg')
    require('./assets/pic-wall/my_room.jpg')
    require('./assets/pic-wall/philarmonie.jpg')
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener('orientationchange', this.handleResize);
  },
  watch: {
    currentState() {
      this.accepted = false;
    }
  },
  methods: {
    handleResize() {
      this.screenClass = window.innerWidth > 960 ? "desktop" : "mobile";
    },
    handleScroll () {
      let totalHeight = 0;
      let state = this.currentState;
      for (var ref in this.$refs) {
        if(this.$refs[ref] && this.$refs[ref].$el) {
          const currentHeight = this.$refs[ref].$el.clientHeight;
          if((totalHeight - window.innerHeight / 2) < window.pageYOffset){
            totalHeight = totalHeight + currentHeight;
            state = ref;
          }
        }
      }
      this.currentState = state;
    },
    deniedSocial(){
      this.accepted = false;
      document.documentElement.style.overflow = 'auto';
    },
    denied(){
      window.location.href = "https://www.youtube.com/watch?v=dQw4w9WgXcQ&autoplay=1&t=42";
    },
    handleClickDependendOnState({state, e}) {
      this.accepted = true;
      switch (state){
        case "social":
            this.$refs.pictureWall.style.top = window.scrollY+ "px";
            document.documentElement.style.overflow = 'hidden';
          break;
        case "professional":
          break;
        case "dating":
          confetti({
            particleCount: 100,
            startVelocity: 30,
            spread: 360,
            origin: {
              x: (e.clientX / window.innerWidth),
              y: (e.clientY / window.innerHeight)
            }
          });
          break;
      }
    }
  }
}
</script>

<style lang="scss">
@import "style/colors.scss";

body{
  margin: 0;
}
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $primary;
}
.site{
  box-sizing: border-box;
  width: 100%;
  min-height: 110vh;
  transition: padding 460ms cubic-bezier(0.43, 0, 0.37, 0.94);
}
.mobile .site{
  padding: 450px 24px 24px;
}
.desktop .site{
  padding: calc(40vh - 200px) #{"min(12vw, 180px)"} 20vh #{"max(38vw, calc(250px + 12vw))"};
}

.content {
  max-width: 720px;
  position: relative;
  z-index: 5;
}
.mobile .content{
  margin: 0 auto 333px;
  max-width: 640px;
}


#pic-wall{
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  z-index: 10;
  left: 0;
  display: grid;
  gap: 0 0;
}
#pic-wall.desktop{
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas:
    ". big_sis . ."
    ". big_sis big_boi ."
    "landscape landscape big_boi .";
}
#pic-wall.mobile{
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas:
    ".       big_boi"
    "big_sis big_boi"
    "big_sis .";
}
.big_boi { grid-area: big_boi; }
.big_sis { grid-area: big_sis; }
.landscape { grid-area: landscape; }
#abort{
  color: rgba(255, 255, 255, 0.96);
  cursor: pointer;
  background: $red-light;
  text-shadow: 1px 1px 2px $red;
  font-size: 4em;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 460ms cubic-bezier(0.43, 0, 0.37, 0.94);
}
#abort:hover{
  text-shadow: 1px 1px 2px $primary;
  box-shadow: inset 0 0 0 12px $red;
}
.mobile #abort {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    text-shadow: 1px 1px 2px $primary;
    box-shadow: inset 0 0 0 0 $red;
  }

  70% {
    text-shadow: 2px 2px 4px $primary;
    box-shadow: inset 0 0 0 12px $red;
  }

  100% {
    text-shadow: 1px 1px 2px $primary;
    box-shadow: inset 0 0 0 0 $red;
  }
}
</style>
