<template>
  <div id="frame" :class="['frame-' + imgID, activeTrigger ? 'active' : '']">
    <div id="pic-header">
      <img id="pic-profile" src="../assets/profil.jpg" />
      <div id="pic-title">
        {{ head }}
      </div>
    </div>
    <div id="img" :class="'img-' + imgID"/>
    <div id="tail">
      <div id="heart">
        {{ likes }} <font-awesome-icon icon="heart" class="icon"/>
      </div>
      <div id="comment">
        {{ comments }} <font-awesome-icon icon="comment" class="icon"/>
      </div>
      <div id="retweet">
        <font-awesome-icon icon="retweet" class="icon"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
name: "Picture",
  props: {
    imgID: {
        type: Number,
        default: () => {
          return Math.floor(Math.random() * 11);
        }
    },
    active: {
      type: Boolean,
      default: false
    }
  },
  created() {
    this.likes = 15 + Math.floor(Math.random() * 32);
    this.comments = Math.floor(Math.random() * 17);
    this.head = this.headCombinations[this.imgID];
    this.frame_class = "show";
  },
  watch: {
    active(newVal){
      setTimeout(() => this.activeTrigger = newVal, 150);
    }
  },
  data(){
    return {
      head: null,
      frame_class: null,
      activeTrigger: null,
      headCombinations: [
          "Till ist hier: Berlin",
          "Till macht ein Selfie",
          "Till wurde auf einem Foto markiert",
          "Till findet das Bild schnieke",
          "Till und seine Familie",
          "Till postet gerne Fotos.",
          "Till will das Foto mit dir teilen",
          "Tills Zimmer"
      ]
    }
  }
}
</script>

